import Jogador from "./Jogador";
import Mestre from "./Mestre";
import Placar from "./Placar";

export default class Bingo {
  constructor({ local, mestre, jogadores, placar }) {
    this.numeros = [];
    this.local = local;
    this.mestre = mestre;
    this.jogadores = jogadores;
    this.placar = placar;
  }
  static comecar() {
    const local = "Botafogo";
    const mestre = new Mestre("Anderson");
    const pedro = new Jogador("pedro")
    pedro.comprarCartela()    
    const luis= new Jogador("luis")
    luis.comprarCartela()
    luis.comprarCartela()

    const placar = new Placar;
    placar.inicializarCartela();
    const jogadores = [pedro,luis ];
    const bingo = new Bingo({ local, mestre, jogadores, placar });
    return bingo;
  }

  comecarPartida() {}

}
