
<template>
  <div class="principal">
    <div>
      <button @click="comecar()">Comerçar</button>

      <div v-if="bingo" >
        <button @click="sortear()">sortear</button>
        <p>{{ bingo.local }}</p>
        <p>Mestre: {{ bingo.mestre.nome }} Ultimo numero Sorteado: {{bingo.placar.ultimoNumero}}</p>
        <p>Placar: {{ bingo.placar.numerosSorteados }} </p>
        <ul>
          <li v-for="(placar, index) of bingo.placar" :key="index">
            {{ placar.numeros}}    
          </li>
        </ul>
        <p>{{ bingo.numeros }}</p>
        <ul>
          <li v-for="(jogador, index) of bingo.jogadores" :key="index">
            {{ jogador.nome }}
            {{ jogador.cartelas }}
          </li>
        </ul>
      </div>

    </div>
    
  </div>
</template>

<script>

import Bingo from "../Model/Bingo";

export default {
  data() {
    return {
      bingo: null,
    };
  },
  methods: {
    comecar() {
      const bingo = Bingo.comecar();

      this.bingo = bingo;
    },
     sortear() {
      this.bingo.placar.sortearNumero();
    },
  },
};
</script>

<style>

.principal{
  display: grid;
  grid-template-columns: 550px 550px 550px;
}
</style>
