
export default class Placar{

    constructor(){
        this.numeros = [];
        this.contadorNumeros = []
        this.numerosSorteados = [];   
    }

    inicializarCartela() {
        const aux = [];
        for(let i=1; i<=99; i++){
            aux.push(i);
        }
        this.numeros = aux.slice();
        this.contadorNumeros = aux.slice();
    }

    adicionarNumeroSorteado(numero){
        this.numerosSorteados.push(numero);
    }

    get ultimoNumero(){
        return this.numerosSorteados[this.numerosSorteados.length - 1];
    }

    sortearNumero(){
        const aux = this.contadorNumeros[Math.floor(Math.random * this.contadorNumeros.length - 1)];
        this.adicionarNumeroSorteado(this.contadorNumeros[aux]);
        this.contadorNumeros.splice(this.contadorNumeros[aux], 1)

    }
}